<template>
    <yd-layout :class="">
        <yd-navbar title="中国银联签报" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/unionPayLabel/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContentFluid bor5">
            <div class="fluidWidth" id="js_changyong">
                <div class="advice">
                    <div class="commonAdvice">
                        常用意见：
                    </div>
                </div>
                <div class="commonopinion">
                    <span :class='{active: opinionIndex == index}' v-for="(item, index) in opinionArr" @click="opinion = item; opinionIndex = index" v-text="item"></span>
                </div>
            </div>
        </div>
        <div class="mainContentFluid bor5">
            <div class="adviceTxt">
                <div class="commonAdvice" v-text="opinionName"></div>
                <!-- <yd-textarea v-model="opinion" placeholder="请输入您的意见" :value="isAgree"></yd-textarea> -->
                <trsTextarea :trsType="true" id="js_pishi" placeholder="请输入您的意见" v-model="opinion" :value="isAgree"></trsTextarea>
            </div>
        </div>
        <div class="mainContentFluid bot_cot new_label">
            <div class="">
                <yd-flexbox direction="vertical">
                    <yd-flexbox-item v-if="liuyanFlag" :class="'fuyan'">
                        <yd-flexbox :class="'goTop'">
                            <div class="postscript nowrap">
                                上一步附言：
                            </div>
                            <div class="postscript_txt" v-html='liuyan'>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item :class="'bgW'" v-if="hiddenOpinion">
                        <div class="opinion_tab" v-if="!bumenFlag" v-show="innerFlag">
                            <span v-bind="{id: ['js_gongsilingdao', 'js_zhubanopinion', 'js_huiqianopinion'][index]}" v-for="(item, index) in yijianArr" @click="opinion_index = index; gongsiLeader = [opinionArr1, opinionArr2, opinionArr3, opinionArr4][index]" v-if="[opinionArr1, opinionArr2, opinionArr3, opinionArr4][index].length > 0" :class="{active: opinion_index == index}" class="zhuban">{{item}}</span>
                        </div>
                        <yd-flexbox>
                            <div class="opinion_content">
                                <yd-flexbox-item>
                                    <yd-flexbox>
                                        <div style="width: 100%;">
                                            <div class="label_box" v-for="item in gongsiLeader">
                                                <div class="range_txt flex">
                                                    <div class="opName fl nowrap" v-text='!!item.partiname ? item.partiname : `${item.userName1}：`'></div>
                                                    <div class="fl opNeirong" v-text="item.neirong"></div>
                                                </div>
                                                <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                            </div>
                                        </div>
                                    </yd-flexbox>
                                </yd-flexbox-item>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <yd-tabbar class="bottom" slot="tabbar" bgcolor="#eee" color="#666">
            <div id="js_fanhui_shenpi" class="backBtn" @click="toBack(2)">
                返回
            </div>
            <div id="wanchengpiyue" class="completeBtn" @click="completeFun">
                完成批阅
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'assistApprove',
    props: ['title'],
    data() {
        return {
            opinionName: '批示意见：',
            statusRadio: 999,
            statusPopup: false,
            opinionIndex: -1,
            opinion_index: 0,
            yijianArr: [],
            opinionArr: [],
            isAgree: '',
            liuyan: '',
            opinio: [],
            opinion: '',
            isLeader: '',
            agreeArr: [],
            liuyanFlag: false,
            leaderTypeArr: [],
            leaderNameArr: [],
            nextId: '',
            leadersId: '',
            approveNum: '',
            comLeaderFlag: false, // 公司领导审批页面
            isApprove: false,
            isFinish: false,
            deparment: '',
            deparmentId: '',
            deptOpinion: [],
            bumenLeader: [],
            gongsiLeader: [],
            opinionArr1: [],
            opinionArr2: [],
            opinionArr3: [],
            opinionArr4: [],
            deptname: '',
            cosnextId: '',
            bumenFlag: false, // 部门收文
            fengsFlag: false,
            typeFlag: 'head',
            hiddenOpinion: true,
            innerFlag: false, // 内部协办
            common: ''
        }
    },
    mounted() {
        const vm = this;
        if (vm.handleInfo.activityID == 'zgsBossAppActivity' || vm.handleInfo.activityID == 'fwZgsBossApproval' || vm.handleInfo.activityID == 'ldsp_Activity') {
            vm.comLeaderFlag = true;
        }
        vm.pageRender();
        document.setTitle(vm.titleName);
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            handleInfo: 'getHandleInfo',
            titleName: 'getTitleName'
        })
    },
    methods: {
        pageRender() {
            let vm = this;
            vm.deptname = vm.userInfo.deptname;
            // 判断是否是领导
            vm.isLeader = Number(vm.userInfo.role);
            if (vm.titleName == '部门收文') {
                vm.bumenFlag = true;
                vm.typeFlag = 'depe';
            } else {
                // 判断是否是分公司签报的发送页面
                vm.userInfo.deptname.indexOf('分公司') != -1 ? vm.typeFlag = 'bran' : vm.typeFlag = 'head';
            }
            if (vm.isLeader == 2) {
                switch (vm.titleName) {
                    case '中国银联签报':
                        vm.opinionArr = ['同意', '退回修改'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '会签意见', '协办意见'];
                        break;
                    case '中国银联发文':
                        vm.opinionArr = ['同意', '退回修改'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '会签意见', '办公室意见'];
                        break;
                    case '中国银联收文':
                        vm.opinionArr = ['已阅', '已阅，尽快落实'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '协办/传阅意见', '办公室意见'];
                        break;
                    case '部门收文':
                        vm.opinionArr = ['已阅', '已阅，退回修改'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '协办/传阅意见', '办公室意见'];
                        break;
                    case '内部协办':
                        vm.opinionArr = ['同意', '退回修改'];
                        vm.yijianArr = ['本部门意见', '需求方意见', '其他协办部门意见'];
                        break;
                }
            } else {
                switch (vm.titleName) {
                    case '中国银联签报':
                        vm.opinionArr = ['同意', '不同意', '退回修改'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '会签意见', '协办意见'];
                        break;
                    case '中国银联发文':
                        vm.opinionArr = ['同意', '不同意', '退回修改'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '会签意见', '办公室意见'];
                        break;
                    case '中国银联收文':
                        vm.opinionArr = ['已阅', '已阅，尽快落实'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '协办/传阅意见', '办公室意见'];
                        break;
                    case '部门收文':
                        vm.opinionArr = ['已阅', '已阅，退回修改'];
                        vm.yijianArr = ['公司领导意见', '主办意见', '协办/传阅意见', '办公室意见'];
                        break;
                    case '内部协办':
                        vm.opinionArr = ['同意', '退回修改'];
                        vm.yijianArr = ['本部门意见', '需求方意见', '其他协办部门意见'];
                        break;
                }
            }
            // 判断是否是内部协办
            if (vm.$route.params.type == 'internalAssist') {
                vm.innerOpinionFun();
                vm.liuyanFlag = false;
            } else {
                vm.opinionFun();
                // 上一步留言
                vm.getH(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                    'type': vm.$route.params.type,
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                }, function(res) {
                    let data = res;
                    if (!data.jsonData) {
                        vm.liuyan = '';
                        vm.liuyanFlag = false;
                    } else {
                        vm.liuyanFlag = true;
                        vm.liuyan = data.jsonData[0].neirong;
                    }
                });
            }
            // 发送审批判断
            for (let key in vm.handleInfo.nextBtn) {
                if (key == 'doApprove') {
                    vm.isApprove = true;
                } else if (key == 'doFinishInner') {
                    vm.isFinish = true;
                    vm.opinionName = '处理意见';
                }
            }
            if (vm.isApprove) {
                // 当有doApprove这个key的时候需要通过leaderTypeArr获取所有领导的名字
                for (let keyJ in vm.handleInfo.nextBtn) {
                    if (keyJ == 'doApprove') {
                        vm.approveNum = vm.handleInfo.nextBtn.doApprove.type;
                        if (vm.handleInfo.nextBtn.doApprove.queryHandlerPerson) {
                            vm.leaderTypeArr = vm.handleInfo.nextBtn.doApprove.queryHandlerPerson.split(','); // 获取领导接口的type
                        } else {
                            // 领导的nextbtn再坐判断 =={doApprove: {4094272: "廖雯", type: "2"}}
                            for (let keyId in vm.handleInfo.nextBtn.doApprove) {
                                if (keyId != 'type') {
                                    vm.leadersId = keyId;
                                }
                            }
                        }
                    } else {
                        vm.nextId = keyJ; // 下一步流程ID
                        vm.deparment = vm.handleInfo.nextBtn[keyJ][1];
                        vm.deparmentId = vm.handleInfo.nextBtn[keyJ][0]; // 部门ID
                    }
                }
                // 送领导审批 的领导名字
                let promise = new Promise(function(resolve, reject) {
                    for (let i = 0; i < vm.leaderTypeArr.length; i++) {
                        vm.getH(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                            'type': vm.$route.params.type,
                            'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                            'queryType': vm.leaderTypeArr[i]
                        }, function(res) {
                            let data = res.jsonData;
                            if (data !== null || data !== undefined) {
                                vm.leaderNameArr = vm.leaderNameArr.concat(data);
                            }
                            if (i == vm.leaderTypeArr.length - 1) {
                                resolve()
                            }
                        });
                    }
                });
                promise.then(function() {
                    vm.leaderNameArr = vm.leaderNameArr;
                });
            } else {
                // 没有doapprove的时候循环key 把key放到一个数组里面然后取第一个为部门，第二个为领导
                let keyArr = [];
                for (let keyK in vm.handleInfo.nextBtn) {
                    keyArr.push(keyK);
                }
                if (keyArr.length > 1) {
                    vm.nextId = keyArr[0]; // 部门下一步id
                    vm.deparment = vm.handleInfo.nextBtn[keyArr[0]][1]; // 部门或者人物名称
                    vm.deparmentId = vm.handleInfo.nextBtn[keyArr[0]][0]; // 部门或者人物Id
                    vm.cosnextId = keyArr[1]; // 领导下一步id
                    vm.leaderName3 = vm.handleInfo.nextBtn[keyArr[1]][1]; // 领导名字
                    vm.leadersId = vm.handleInfo.nextBtn[keyArr[1]][0]; // 领导id
                    vm.leaderNameArr = [{ 'empname': vm.leaderName3, 'operatorid': vm.leadersId }];
                    vm.leaderName3 = '(' + vm.handleInfo.nextBtn[keyArr[1]][1] + ')'; // 领导名字
                } else {
                    vm.leadersId = vm.handleInfo.nextBtn[keyArr[0]][0];
                    vm.nextId = keyArr[0];
                }
            }
        },
        innerOpinionFun() {
            const vm = this;
            let ajaxArr = [];
            if (vm.userInfo.deptname.indexOf('（') != -1) {
                vm.userInfo.deptname = vm.userInfo.deptname.substring(0, vm.userInfo.deptname.indexOf('（'));
            }
            ajaxArr = [1, 2, 3].map((item, index) => {
                return vm.$http.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryApproveByWorkItemID', {
                    params: {
                        'type': vm.$route.params.type,
                        'orgName': vm.userInfo.deptname,
                        'type1': index + 1,
                        'workItemID': vm.$route.params.workItemID,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'random': Math.floor(Math.random() * (100000 + 1))
                    }
                });
            });
            vm.$dialog.loading.open('加载中...');
            const promise = new Promise(function(resolve, reject) {
                vm.$http.all(ajaxArr).then(vm.$http.spread(function(...res) {
                    vm.opinionArr1 = res[0].data.jsonData; // 本部门意见
                    if (vm.$route.params.activeId != 'intaFillReq') {
                        vm.opinionArr2 = res[1].data.jsonData; // 需求方意见
                        vm.opinionArr3 = res[2].data.jsonData; // 其他协办部门意见
                    }
                    vm.innerFlag = true;
                    if (vm.opinionArr1.length < 1 && vm.opinionArr2.length < 1 && vm.opinionArr3.length < 1) {
                        vm.hiddenOpinion = false;
                    }
                    resolve(true);
                    vm.$dialog.loading.close();
                })).catch(error => {
                    vm.$dialog.loading.close();
                    // vm.$dialog.toast({
                    //     mes: '系统正在开小差，请稍后再试',
                    //     timeout: 1500
                    // });
                    console.log(error);
                });
            });
            promise.then(function(data) {
                if (vm.opinionArr1.length > 0) {
                    vm.opinion_index = 0;
                    vm.gongsiLeader = vm.opinionArr1;
                } else {
                    if (vm.opinionArr2.length > 0) {
                        vm.opinion_index = 1;
                        vm.gongsiLeader = vm.opinionArr2;
                    } else {
                        if (vm.opinionArr3.length > 0) {
                            vm.opinion_index = 2;
                            vm.gongsiLeader = vm.opinionArr3;
                        }
                    }
                }
            });
        },
        opinionFun() {
            const vm = this;
            vm.innerFlag = true;
            let repeat = Symbol('repeat'); // 判断办公室的重复意见
            // 领导意见
            function leaderOP() {
                return vm.$http.get('/portalMgmt/oa/menus/getJson', {
                    params: {
                        'type': vm.$route.params.type,
                        'workItemID': vm.$route.params.workItemID,
                        'urlType': 1,
                        'random': Math.floor(Math.random() * (100000 + 1))
                    }
                });
            }
            // 部内意见
            function deptOP() {
                return vm.$http.get('/portalMgmt/oa/menus/getJson', {
                    params: {
                        'type': vm.$route.params.type,
                        'workItemID': vm.$route.params.workItemID,
                        'urlType': 2,
                        'random': Math.floor(Math.random() * (100000 + 1))
                    }
                });
            }
            vm.$dialog.loading.open('加载中...');
            vm.$http.all([leaderOP(), deptOP()]).then(vm.$http.spread(function(data1, data2) {
                let data = data1.data.jsonData;
                data2.data.jsonData == '' ? '' : data2.data.jsonData.map((item, index) => {
                    item.activityDefName == '事务助理处理' ? item.partiname = '' : item.partiname = `${item.partiname}:`;
                    return item;
                });
                if ((data == '' || data[0].opinions.length <= 0) && data2.data.jsonData == '') {
                    vm.hiddenOpinion = false;
                }
                if (data.length > 0) {
                    data.forEach((item, index) => {
                        if (item.type == '1') {
                            vm.opinionArr1 = item.opinions;
                        } else if (item.type == '2') {
                            vm.opinionArr2 = item.opinions;
                        } else if (item.type == '3') {
                            vm.opinionArr3 = item.opinions;
                        } else if (item.type == '4') {
                            vm.opinionArr4 = item.opinions;
                        }
                    });
                }
                if (data2.data.jsonData != '' && data2.data.jsonData != undefined) {
                    vm.opinionArr1 = [...vm.opinionArr1, ...data2.data.jsonData.filter((item) => item.type == '1')];
                    vm.opinionArr2 = [...vm.opinionArr2, ...data2.data.jsonData.filter((item) => item.type == '2')];
                    vm.opinionArr3 = [...vm.opinionArr3, ...data2.data.jsonData.filter((item) => item.type == '3')];
                    vm.opinionArr4 = [...vm.opinionArr4, ...data2.data.jsonData.filter((item) => item.type == '4')];
                }
                vm.opinionArr4.forEach((item, index, arr) => {
                    if (index > 0 && item.neirong == arr[index - 1].neirong) {
                        arr[index].partiname = `${arr[index - 1].partiname.replace(':', '')}/${item.partiname}`;
                        arr[index - 1].neirong = repeat;
                    }
                });
                vm.opinionArr4 = vm.opinionArr4.filter((item, index) => item.neirong != repeat);
                if (vm.bumenFlag) {
                    vm.hiddenOpinion = false;
                } else {
                    if (vm.opinionArr1.length > 0) {
                        vm.opinion_index = 0;
                        vm.gongsiLeader = vm.opinionArr1;
                    } else {
                        if (vm.opinionArr2.length > 0) {
                            vm.opinion_index = 1;
                            vm.gongsiLeader = vm.opinionArr2;
                        } else {
                            if (vm.opinionArr3.length > 0) {
                                vm.opinion_index = 2;
                                vm.gongsiLeader = vm.opinionArr3;
                            } else {
                                if (vm.opinionArr4.length > 0) {
                                    vm.opinion_index = 3;
                                    vm.gongsiLeader = vm.opinionArr4;
                                }
                            }
                        }
                    }
                }
            })).catch(error => {
                vm.$dialog.loading.close();
                // vm.$dialog.toast({
                //     mes: '系统正在开小差，请稍后再试',
                //     timeout: 1500
                // });
                console.log(error);
            });
        },
        toBack(type) {
            this.$emit('botSetflag', type);
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        completeFun() {
            // 完成审批
            let vm = this;
            if (vm.opinion.replace(' ', '') != '') {
                if (vm.isApprove) {
                    vm.getH(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                        'type': vm.$route.params.type,
                        'approveType': vm.approveNum,
                        'workItemID': vm.$route.params.workItemID,
                        'flag': vm.typeFlag,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'leaderIDs': vm.leadersId,
                        'workTitle': vm.title,
                        'opinion': vm.opinion
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '完成审批',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function() {
                                    vm.$router.push('/handle')
                                }, 0)
                            }
                        });
                    });
                } else if (vm.isFinish) {
                    vm.getH(vm.conf.portalMobile + '/API/WorkFlow/oawf/doFinishInner', {
                        'type': vm.$route.params.type,
                        'workItemID': vm.$route.params.workItemID,
                        'flag': vm.typeFlag,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'opinion': vm.opinion
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '完成审批',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function() {
                                    vm.$router.push('/handle')
                                }, 0)
                            }
                        });
                    });
                } else {
                    vm.getH(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                        'type': vm.$route.params.type,
                        'workItemID': vm.$route.params.workItemID,
                        'leaderIDs': vm.leadersId,
                        'activityID': vm.nextId,
                        'opinion': vm.opinion,
                        'flag': vm.typeFlag,
                        'workTitle': vm.title,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '完成审批',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function() {
                                    vm.$router.push('/handle')
                                }, 0)
                            }
                        });
                    });
                }
            } else {
                vm.$dialog.toast({
                    mes: '请输入意见内容',
                    timeout: 2000
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
.range_txt {
    margin-top: 0;
    overflow: hidden;
    padding-left: .1rem;
}

.range {
    padding-left: .1rem;
    font-size: .28rem;
    color: #c5c5c5;
}

.bot_cot {
    padding: 0 .18rem;
    background: #e2e2e2!important;
}

.new_label .bgW {
    padding: 0 .17rem;
}

.new_label .opinion_tab {
    overflow: scroll;
    width: 100%;
    white-space: nowrap;
}

.new_label .opinion_tab span {
    margin-right: .2rem;
    height: .56rem;
    line-height: .56rem;
    padding: 0 .3rem;
    margin-bottom: .3rem;
}

.fuyan {
    background: #fff;
    border: none;
    border-radius: .06rem;
    margin-bottom: .18rem;
}

.commonopinion {
    padding: 0 .33rem;
    span {
        display: inline-block;
        height: .56rem;
        border: 1px solid #bfbfbf;
        color: #666;
        font-size: .32rem;
        line-height: .54rem;
        border-radius: .28rem;
        padding: 0 .36rem;
        margin-right: .3rem;
        margin-bottom: .3rem;
        &.active {
            background: #6cbbfb;
            color: #fff;
            border-color: #6cbbfb;
        }
        &:last-child {
            margin-right: 0;
        }
    }
}

.bgW {
    background: #fff;
}

.advice {
    border-bottom: none;
}

.advice,
.adviceTxt,
.compl_back,
.yijian,
.fuyan {
    padding: 0 .33rem;
}

.flex {
    display: flex;
}

.nowrap {
    white-space: nowrap;
}

.fuyan {
    padding-bottom: .3rem;
}

.fuyan .postscript {
    padding-top: .3rem;
}

.adviceTxt {
    padding-top: .15rem;
    min-height: 3.8rem;
}

.selectAdvice {
    color: #999;
